.catalog-page, .subcategory-page, .brands, .services-card, .services-page{
    .feedback{
        margin-top: 40px;
        &__inner {
            .Btn{
                margin-left: 20px;
            }
        }
        &-brims{
            margin-left: 20px;
        }
    }
}
.catalog-page, .subcategory-page {
    .catalog-block {
        margin-top: 0;
    }
}
.subcategory-page{
    .feedback{
            &__inner {
                .section-title{
                    font-size: 20px;
                }
            }
        }
    }
    .sorting:first-child{
        .dropdown{
            li:nth-child(3), li:nth-child(4) {
                span::after{
                    position: absolute;
                    content: "\E008";
                    font-family: "icons";
                    font-size: 5px;
                    top: 9px;
                    right: -17px;
                }
            }
            li:nth-child(3) span::after{
                transform: rotate(180deg);
            }
            span{
                position: relative;
            }
        }
    }
@media(max-width: 1700px){
    .subcategory-page, .brands, .services-card, .services-page{
        .feedback{
            &__inner {
                .input{
                    width: 195px;
                }
            }
        }
    }
}
@media(max-width: 1600px){
    .catalog-page{
        .feedback{
            &__inner {
                .input{
                    width: 195px;
                }
            }
        }
    }
    .subcategory-page{
        .feedback{
            &__inner {
                .section-title{
                    font-size: 17px;
                }
            }
        }
    }
}
@media(max-width: 1199px){
    .catalog-page, .subcategory-page, .brands, .card-product__page, .services-card, .services-page{
        h1.section-title{
            margin-bottom: 30px;
        }
        .feedback{
            &__inner {
                .section-title{
                    font-size: 22px;
                }
                .input{
                    width: 100%;
                }
                .Btn{
                    margin-left: 10px;
                }
            }
        }
    }
}
@media(max-width: 767px){
    .catalog-page, .subcategory-page {
        .feedback{
            margin-top: 20px;
            &__inner .Btn{
                margin-left: 10px;
            }
        }
    }
    .catalog-page .catalog-block, .subcategory-page .catalog-block {
        margin-top: 0;
    }
}
@media(max-width: 600px){
    .catalog-page, .subcategory-page {
        .feedback__inner {
            .Btn{
                margin-left: 0;
            }
            .section-title{
                font-size: 21px;
            }
        }
    }
}
@media(max-width: 575px){
    .catalog-page, .subcategory-page{
        h1.section-title{
            margin-bottom: 20px;
        }
    }
}